.App {
  text-align: center;
  height: 100%;

  overflow: hidden;
}

.map{
  height: 100%;
  background-color: #0088cc;
  z-index: 0;
}
.map img{
  width:30%;
  height: auto;
  margin: 10px;
}
.mapTour{
  width: 95%;
  height: 50%;
  margin: auto 20px;
  position: absolute;
  background-color: #0088cc;
  top: 20%;
  left: 0;
  display: none;
}
.toolbar{
  position: absolute;
  left: 20px;
  top:20px;

  width:30px;
}
.slidesWrap{
  overflow: auto;


  position: absolute;
  left: 0;
  bottom: 20px;
  width: 100%;
  height: 120px;
}
.slides{
  position: absolute;

  width: 1200px;
  height: 120px;
  overflow-x: auto;
  overflow-y: hidden;
  text-align: left;
  margin-left: 20px;

  -ms-flex-direction: row;

      flex-direction: row;
}
.slides .card{
  width: 240px;
  height: 110px;

  border: 2px solid white;
  border-radius: 5px;

  margin-right: 20px;

  background-color: #222222;

  display: inline-block;


}



